html {
    width: 100%;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    scroll-behavior: smooth;
    overflow-x: hidden;
    
}

html::-webkit-scrollbar {
    display: none;
}

img{
    user-drag: none; 
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

.App{
    width: 100%;
    height: 100%;
}

.navbar{
    position: fixed;
    top: 0;
    height: 50px;
    width: 100%;
    background-color: #0008;
    z-index: 1;
}

.nav-list{
    flex-direction: row;
    float: right;
    width: 100%;
}

.nav-list li{
    float: right;
    list-style: none;
    margin-right: 10%;
    cursor: pointer;
    color: #eee
}

.nav-list li a{
    text-decoration: none;
    color: #eee
}

.nav-list li:hover{
    color: #eee5;
}

.parallax-div{
    height: 100vh;
    width: fit-content;
    margin: auto;
    
}

.parrallax-text{
    color: white;
    font-size: 50px;
    background-color: #0008;
    padding: 10px;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%,-50%);
}

.about-div{
    width: fit-content;
    display: flex;
    flex-direction: row-reverse;
    position: relative;
    
}

.main-title{
    width: 50%;
    float: right;
    margin-top: 12%;
    color: #fff;
    text-align: center;
    font-size: 2.5rem;
    text-decoration: underline;
}

.title-text{
    width: 40%;
    float: right;
    color: #0008;
    margin-left: 10%;
    font-size: 1.5rem;
    padding-right: 30px;
}

.block{
    display: flex; /* required */
    flex-flow: row nowrap; /* required */
}



.block .main-img{
    height: auto;
    width: 40%;
    margin-top: 10%;
    margin-left: 5%;
}

.block .bg-div{
    overflow: hidden;
    height: 70vh;
    background: #0008;
}


