.projects{
    margin-top: 20vh;
    width: 100%;
}

.projects-title{
    text-decoration: underline;
    color: #0009;
    width: fit-content;
    margin: auto;
    margin-top: 5vh;
    margin-bottom: 3vh;
    font-size:4rem
}

.gtitle{
    
    color: #0008;
    font-size: 2rem;
    width: fit-content;
    margin: auto;
    margin-bottom: 2%;   
    margin-top: 2%;
    text-decoration: underline;
}

.gImg{
    -webkit-transform:rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    width: 50%;
    margin-left: 50%;
    transform: translate(-50%) rotate(90deg);
    margin-bottom: 10%;
}

.project-img{
    width: 100%;
}


.sliders{
    margin-left: -22%;
}
.slider-div{
    position: relative;
    margin-bottom: 25vh;
    margin-left: 20%;
    width: 80%;
}

.slide-container{
    width: 50%;
}

.slider-title{
    color: #0008;
    width: fit-content;
    margin: auto;
    font-size: 2.5rem;
    background-color: #fff8;
    top: 0;
    left: 0;
    margin-bottom: 5%;
}

.footer{
    height: 100.5vh;
    background-color: #0008;
    color: white;
}

.footer-content{
    position: absolute;
    left: 50%;  
    transform: translate(-50%);
    width: 30%;
    margin: auto;
}

.footer-signup-title{
    position: relative;
    width: fit-content;
    font-size: 3rem;
    color: white;
    margin: auto;
    margin-top: 20vh;
}

.footer-signup-text{
    margin-top: 10vh;
    
}

.footer-signup-inpts{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: auto;
}

.footer-signup-inpt{
    margin-top: 3vh;
    border-radius: 10px;
    border: none;
    font-size: 2rem;
    width: 100%;
}

.footer-signup-inpt:focus, .footer-signup-btn:focus{
    outline: none;
}

.footer-signup-btn:hover{
    background-color: #0002;
}

.footer-signup-btn{
    width: 100px;
    height: 30px;
    margin: auto;
    margin-top: 3vh;
    border-radius: 10px;
    border: none;
}

form{
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 1400px) {
    .block .main-img{
        margin-top: 13%;
    }
}

@media screen and (max-width: 1110px) {
    .parallax-div{
        height: 80vh;
    }

    .parrallax-text{
        font-size: 2rem;
    }

    .block .main-img{
        margin-top: 10%;
    }

    .main-title{
        width: fit-content;
        float: none;
        margin: auto;
        margin-top: 10vh;
    }

    .title-text{
        width: 80%;
        color: #0008;
        font-size: 1.5rem;
        padding: 0;
        float: none;
        margin: auto;
        margin-top: 5vh;
    }

    .block{
        display: flex; /* required */
        flex-flow: column nowrap; /* required */
    }
    
    .bg-div{
        width: 100%;
        height: 100%;
        margin: auto;
    }
    
    .block .bg-div{
        height: 120vh;
        background: #0008;
    }

    .block .main-img{
        border-radius: 10px;
        width: 80%;
        margin-left: 10%;
    }

    .sliders{
       margin-left: -25%;
    }
   
}

@media screen and (max-width: 800px) {
    .parallax-div{
        height: 50vh;
    }

    .gtitle{
        font-size: 1.5rem;
    }
    
    .parrallax-text{
        font-size: 2rem;
    }

    .projects-title{
        font-size: 3rem;
    }
    
    .nav-list{
        width: 100%
    }

    .slide-container{
        width: 80%!important;
    }
    .footer-signup-title{
        font-size: 3rem;
    }

    .footer-content{
        width: 50%;
    }
}

@media screen and (max-width: 450px) {

    .projects-title{
        font-size: 2.3rem;
    }

    .slider-title{
        font-size: 2rem;
    }

    .gtitle{
        font-size: 1.3rem;
    }

    .footer-signup-title{
        font-size: 2rem;
    }

    .parrallax-text{
        width: 80%;
    }
}