.NavbarItems{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    background: #383838;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    color: white;
    z-index: 12;
}

.navbar-logo{
    margin-left: 30px;
    width: 400px;
    justify-self: start;
    cursor: pointer;
}

.menu-icon {
    cursor: pointer;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10%;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links{
    transform: translate(-5%);
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}


.menu-icon{
    display: none;
}

.placeholder{
    width: 100%;
    height: 100vh;
    position: absolute;
    bottom: 0;
    background-color: white;
    opacity: 0.4;
    overflow: hidden;
}

@media screen and (max-width: 1066px){

    .NavbarItems {
        position: fixed;
    }

    .nav-menu{
        display: none;
        width: 100%;
        height: 550px;
        position: absolute;
        top: 57px;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active{
        display: flex;
        flex-direction: column;
        background: #383838;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover{
        background-color: #272727;
        border-radius: 0;
    }

    .navbar-logo{
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(15%, 10%);
    }

    .menu-icon{
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem; 
        cursor: pointer;
    }
}  

@media screen and (max-width: 560px){
    .navbar-logo{
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(15%, 20%);
        width: 300px;
    }
    .nav-links{
        margin-left: -10%;
    }
}

@media screen and (max-width: 440px){
    .navbar-logo{
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(15%, 60%);
        width: 200px;
    }
}